html, body {
  height: 100vh;
  width: 100%;
  font-size: 10px;
}

body {
  background: #62280B url(/pizza/static/media/table.cf22bd5f.jpg) repeat center center;
  background-size: cover;
}
#reactive-pizza-table {
  align-items: center;
  justify-content: center;
  display: flex;
  height: auto;
  width: 100%;
}

@media screen and (max-width: 1023px) {
  #reactive-pizza-table {
    flex-direction: column;
  }
}
#ingredients-list-container {
  background-color: #fff;
  border-radius: 10px;
  padding: 1rem 3rem;
  margin-left: 5rem;
  border: 3px solid orange;
}
#ingredients-list-container h2 {
  color: orange;
  font: normal 3rem/1.5 'Pacifico', cursive;
  margin: 0;
  text-shadow: 1px 1px 0px #d40;
}
.IngredientList {
  background-color: transparent;
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.Ingredient img {
  display: inline-block;
  height: 5rem;
  margin-right: 1rem;
  width: 5rem;
}
.Ingredient {
  cursor: pointer;
  display: flex;
  padding: .5rem;
  position: relative;
  width: 100%;
}
.Ingredient div {
  display: flex;
  align-items: center;
}
.Ingredient::before {
  color: #ccc;
  content: "\2713";
  display: block;
  font-size: 4rem;
  padding-top: .9rem;
  text-shadow: 1px 1px 0 #bbb;
  width: 4rem;
  font-weight: bold;
}
.Ingredient.used {
  /*background-color: #C6F4C6;*/
}
.Ingredient.used::before {
  color: #00A604;
  text-shadow: 1px 1px 0 #004400;
}
.Ingredient span {
  display: inline-block;
  margin-right: 1rem;
  font: normal 2rem/1.5 'Pacifico', cursive;
}
.Ingredient span.ingredient-name {
  color: #333;
}
.Ingredient span.ingredient-price {
  color: #d00;
}

@media screen and (max-width: 1023px) {
  .IngredientList {
    flex-flow: row wrap;
    flex-basis: 50px;
    margin: 0;
  }
  .Ingredient {
    width: auto;
  }
  .Ingredient::before {
    position: absolute;
    left: 1.7rem;
    display: none;
    font-size: 5rem;
  }
  .Ingredient.used::before {
    display: block;
  }
  #ingredients-list-container {
    margin: 0 0 2rem 0;
  }
  #ingredients-list-container h2 {
    display: none;
  }
  .Ingredient span.ingredient-price,
  .Ingredient span.ingredient-name {
    display: none;
    order: 1;
  }
}
.OrderForm {
  background: rgba(0, 0, 0, .8);
  display: none;
  font: bold 1.8rem/1 'Open Sans', sans-serif;
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
}
.OrderForm.opened {
  display: flex;
  align-items: center;
  justify-content: center;
}

.OrderForm .order-inner {
  background: white;
  border-radius: 3px;
  color: #222;
  padding: 2rem;
  position: relative;
  width: 50%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: none;
}
.OrderForm h2,
.OrderForm h3 {
  color: #e74c3c;
  font: normal 5rem/1 'Pacifico', cursive;
  margin-top: 1rem;
  white-space:nowrap;
}
.OrderForm h3 {
  font-size: 3rem;
  margin: 2rem 0 1rem;
}
#btn-order-close {
  background: transparent;
  border: none;
  color: #787878;
  font-size: 8rem;
  font-weight: 100;
  line-height: 1;
  position: absolute;
  right: 1rem;
  top: 1rem;
}
#order-form {
  display: flex;
  flex-direction: column;
}

#order-form select,
#order-form textarea,
#order-form input {
  border-style: solid;
  border-radius: 3px;
  line-height: 2;
  margin: 1rem 0;
  padding: 1rem;
}
#order-form input:not([type=submit]):valid {
  border: 1px solid green;
}
#order-form .disclaimer {
  color: #666;
}
#btn-order-submit {
  border-radius: 3px;
  padding: 10px 42px;
  font: normal 2rem/1 'Open Sans', sans-serif;
  color: #fff;
  text-align: center;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
  background: #e74c3c;
  border: 0;
  cursor: pointer;
}

.table-order-ingredients {
  width: 100%;
}
.table-order-ingredients th {
  background-color: #eee;
  font-weight: bold;
  text-align: left;
}
.table-order-ingredients th,
.table-order-ingredients td {
  border-bottom: 1px solid #ccc;
  padding: 10px;
}
.table-order-ingredients .total-row td {
  background-color: #eee;
  font-size: 2rem;
  font-weight: bold;
}
@media screen and (max-width: 1023px) {
  .OrderForm .order-inner {
    width: 80%;
  }
}
@media screen and (max-width: 767px) {
  .OrderForm.opened {
    align-items: flex-start;
  }
  .OrderForm .order-inner {
    width: 100%;
  }
  #order-form .field {
    flex-direction: column;
    align-items: flex-start;
  }
  #order-form .field label {
    flex-basis: 100%;
    width: 100%;
    text-align: left;
  }
  #order-form .field select,
  #order-form .field textarea,
  #order-form .field input {
    flex-basis: 100%;
    width: 100%;
  }
}
@media screen and (max-width: 360px) {
  .OrderForm h2 {
    margin-top: 6rem;
  }
}
#Pizza-container {
  display: flex;
  flex-flow: column;
}
#Pizza-base {
  background: transparent url(/pizza/static/media/pizza-base.ba7bf914.svg) no-repeat center center; 
  height: 600px;
  width: 600px;
  position: relative;
}
#Pizza-base:before {
  box-shadow: 5px 5px 10px rgba(0, 0, 0, .77);
  border-radius: 50%;
  content: "";
  display: block;
  height: 600px;
  width: 600px;
}
#Pizza-base div {
  width: 600px;
  height: 600px;
  position: absolute;
  top: 0;
  background-color: transparent;
  background-position: center center;
  background-repeat: no-repeat;
  z-index: 5;
}
#Pizza-base div[data="10"] {
  z-index: 1;
}
.Pizza-ingredient-1 {
  background-image: url(/pizza/static/media/base-olive.9357fdc4.svg);
}
.Pizza-ingredient-2 {
  background-image: url(/pizza/static/media/base-paprika.5f179e47.svg);
}
.Pizza-ingredient-3 {
  background-image: url(/pizza/static/media/base-salami.7e63c108.svg);
}
.Pizza-ingredient-4 {
  background-image: url(/pizza/static/media/base-tomato.f58732ba.svg);
}
.Pizza-ingredient-5 {
  background-image: url(/pizza/static/media/base-mushroom.e3399c38.svg);
}
.Pizza-ingredient-6 {
  background-image: url(/pizza/static/media/base-onion.964a0b60.svg);
}
.Pizza-ingredient-7 {
  background-image: url(/pizza/static/media/base-parsley.1d6c9d55.svg);
}
.Pizza-ingredient-8 {
  background-image: url(/pizza/static/media/base-pineapple.67f2b2a5.svg);
}
.Pizza-ingredient-9 {
  background-image: url(/pizza/static/media/base-shrimp.557900c6.svg);
}
.Pizza-ingredient-10 {
  background-image: url(/pizza/static/media/base-sauce.3d881fc7.svg);
}
#Pizza-base div[data=10] {
  z-index: 1 !important;
}
@media screen and (max-width: 1023px) {
  #Pizza-container {
    order: 2;
  }
}

@media screen and (max-width: 609px) {
  #Pizza-base,
  #Pizza-base:before,
  #Pizza-base div {
    background-size: cover;
    width: 400px;
    height: 400px;
  }
}

@media screen and (max-width: 399px) {
  #Pizza-base,
  #Pizza-base:before,
  #Pizza-base div {
    background-size: cover;
    width: 300px;
    height: 300px;
  }
}
#TopPanel {
  padding: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  background: rgba(255, 255, 255, .95);
  margin-bottom: 6rem;
  border-bottom: 1px solid #aaa;
}
#logo-img {
  width: 60px;
  height: 56px;
  margin-right: 1.5rem;
}
#TopPanel h1 {
  font: normal 4rem/1 'Pacifico', cursive;
  margin: 0;
  color: orange;
  margin-right: auto;
  text-shadow: 1px 1px 0px #d40;
  white-space:nowrap;
}
#order-price {
  font: bold 3rem/1 'Open Sans', sans-serif;
  margin-right: 5rem;
  color: #c00;
}

#order-button {
  border-radius: 3px;
  padding: 10px 42px;
  font: normal 2rem/1 'Open Sans', sans-serif;
  color: #fff;
  text-align: center;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
  background: #e74c3c;
  border: 0;
  cursor: pointer;
}
#order-button:hover {
  background-color: #E7786D;
}

@media screen and (max-width: 1023px) {
  #TopPanel {
    margin-bottom: 2rem;
  }
  #TopPanel h1 {
    display: none;
  }
  #logo-img {
    margin-right: auto;
  }
}

@media screen and (max-width: 609px) {
  #order-price {
    margin-right: 1rem;
  }
}
